import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { isEqual } from 'lodash';
import { combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay, skip, takeUntil } from 'rxjs/operators';
import { IDataPageResult } from '@helix/platform/shared/api';
import { RxWizardModalComponent } from '@helix/platform/shared/components';
import { IRowDataItem } from '@helix/platform/view/api';
import {
  IRecordGridColumn,
  IRecordGridConfig,
  IRecordGridDataPageParams,
  RecordGridComponent,
  RowSelectionMode
} from '@helix/platform/view/components';
import { IRecordDefinition, RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { RxExternalDataService } from '../../../services/external-data/external-data.service';
import { IExternalRecordWizardContext } from '../external-record-wizard.types';

@Component({
  selector: 'ax-table-selection-step',
  templateUrl: 'table-selection-step.component.html',
  styleUrls: ['./table-selection-step.component.scss']
})
export class TableSelectionStepComponent implements OnInit, OnDestroy {
  @Input()
  context: IExternalRecordWizardContext;

  @ViewChild('tableSelectionStepRecordGrid', { static: true })
  tableSelectionStepRecordGrid: RecordGridComponent;

  sectionLabel: string;
  tableSelectionRecordGridConfig$: Observable<IRecordGridConfig>;

  private destroyed$ = new ReplaySubject<boolean>(1);
  private selectedRow;

  constructor(
    private rxExternalDataService: RxExternalDataService,
    private rxWizardModalComponent: RxWizardModalComponent,
    private translateService: TranslateService
  ) {}

  ngOnInit() {
    this.sectionLabel = this.translateService.instant(
      'com.bmc.arsys.rx.innovation-studio.external-record-wizard.table-selection.label'
    );

    const tableSelectionStepContext$ = this.rxWizardModalComponent.context$.pipe(
      shareReplay(1),
      takeUntil(this.destroyed$)
    );

    tableSelectionStepContext$
      .pipe(
        map((stepContext) => stepContext.dataSourceName),
        distinctUntilChanged(isEqual),
        skip(1)
      )
      .subscribe(() => {
        this.selectedRow = null;
        this.rxWizardModalComponent.api.updateContext({ tableName: '' });
        this.tableSelectionStepRecordGrid.api.refresh().subscribe();
      });

    const isCurrentStep$ = combineLatest([
      this.rxWizardModalComponent.steps$,
      this.rxWizardModalComponent.tabIndex$
    ]).pipe(
      map(([steps, tabIndex]) => steps[tabIndex].id === 'table-selection'),
      takeUntil(this.destroyed$)
    );

    const changedTableName$ = tableSelectionStepContext$.pipe(
      map((stepContext) => stepContext.tableName),
      distinctUntilChanged(isEqual)
    );

    combineLatest([isCurrentStep$, changedTableName$])
      .pipe(
        filter(([isCurrentStep, externalTableName]) => isCurrentStep),
        distinctUntilChanged(isEqual)
      )
      .subscribe(([isCurrentStep, externalTableName]) => {
        if (externalTableName) {
          this.rxWizardModalComponent.api.enableNextButton();
        } else {
          this.rxWizardModalComponent.api.disableNextButton();
        }
      });

    const gridColumns: IRecordGridColumn[] = [
      {
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.name.label'),
        fieldId: 'name'
      }
    ];

    this.tableSelectionRecordGridConfig$ = of({
      columns: gridColumns,
      enableColumnSelection: false,
      enableFiltering: false,
      enableRowSelection: RowSelectionMode.Single,
      recordIdField: 'name',
      styles: 'flex-fill',
      useExternalFiltering: true,
      emptyStateLabelText: this.translateService.instant(
        'com.bmc.arsys.rx.innovation-studio.external-record-wizard.table-selection.grid.empty-state.label'
      ),
      getRecordDefinition: (): Observable<IRecordDefinition> =>
        of({
          fieldDefinitions: [
            {
              id: 'name',
              resourceType: RX_RECORD_DEFINITION.resourceTypes.character
            }
          ]
        }),
      getData: (queryParams: IRecordGridDataPageParams): Observable<IDataPageResult> =>
        this.context.dataSourceName
          ? this.rxExternalDataService.getArsTables(
              queryParams.pageSize,
              queryParams.startIndex,
              this.context.dataSourceName,
              queryParams.searchText,
              queryParams.sortBy
            )
          : of({
              totalSize: 0,
              data: []
            })
    });

    this.tableSelectionStepRecordGrid.rowSelectionChanged
      .pipe(
        skip(2), // first time there are no columns, second time there are no rows selected
        takeUntil(this.destroyed$)
      )
      .subscribe((selectedRows: IRowDataItem[]) => {
        if (selectedRows.length) {
          this.selectedRow = selectedRows[0];
          this.rxWizardModalComponent.api.updateContext({ tableName: this.selectedRow.name });
        } else if (this.selectedRow) {
          this.tableSelectionStepRecordGrid.api.setSelectedRows(this.selectedRow);
        }
      });
  }

  ngOnDestroy() {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }
}
